<template>
  <div class="top">
    <div class="plan" v-for="(item, index) of tabsList" :key="index">
      <div @click="goto(item)">{{ item.name }}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},

  data() {
    return {
      tabsList: [
        // {
        //   id: 1,
        //   name: "方案一",
        // },
        // {
        //   id: 2,
        //   name: "方案二",
        // },
        // {
        //   id: 3,
        //   name: "方案三",
        // },
      ],
    };
  },

  created() {
    console.log(this.$route);
    this.tabsList = JSON.parse(this.$route.query.tab);
  },
  mounted() {},
  methods: {
    goto(item) {
      console.log(item);
      let string = JSON.stringify(item);
      this.$router.push({
        path: "/insuranceInformation",
        query: {
          KDPC:string,
        },
      });
    },
  },
  watch: {},
  computed: {},
};
</script>

